.loader-container {
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 2;
  flex: 1 1 auto;
}

.loader {
  z-index: 3;
}

.large {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  backdrop-filter: blur(5px);
}